@font-face {
  font-family: "Arsilon";
  src: url("/fonts/Arsilon.otf");
}
body {
  font-family: "Poppins";
  font-weight: 400;
}

h1, h2, h3, h4, h5 {
  font-family: "Poppins";
  font-weight: 700;
}

html {
  font-size: 100%;
} /* 16px */
h1 {
  font-size: 4.21rem; /* 67.36px */
}

h2 {
  font-size: 3.158rem; /* 50.56px */
}

h3 {
  font-size: 2.369rem; /* 37.92px */
}

h4 {
  font-size: 1.777rem; /* 28.48px */
}

h5 {
  font-size: 1.333rem; /* 21.28px */
}

small {
  font-size: 0.75rem; /* 12px */
}

header {
  z-index: 5;
  padding: 1em;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  width: max-content;
}
.logo img {
  mix-blend-mode: multiply;
}

.menu {
  display: flex;
  gap: 0.6666666667em;
  position: relative;
}
.menu * {
  cursor: pointer;
}
.menu label {
  position: relative;
}
.menu label::after {
  content: "Menu";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: calc(100% + 0.6666666667em);
}
.menu label div {
  width: 20px;
  height: 2px;
  background: var(--rich-black);
}
.menu label div:nth-child(2) {
  margin: 3px 0;
}
.menu input {
  position: absolute;
  opacity: 0;
}

footer {
  padding: 1.75em 0;
}

.copyright {
  display: flex;
  justify-content: center;
  gap: 4px;
}

/*Desktop First*/
.container {
  max-width: 1536px;
  margin: 0 auto;
}

/*2xl*/
@media (max-width: 1536px) {
  .container {
    max-width: 1280px;
  }
}
@media (min-width: 1536px) {
  .container {
    max-width: 90%;
  }
}
/*xl*/
@media (max-width: 1280px) {
  .container {
    max-width: 1024px;
  }
}
/*lg*/
@media (max-width: 1024px) {
  .container {
    max-width: 768px;
  }
}
/*md*/
@media (max-width: 768px) {
  .container {
    max-width: 640px;
  }
}
/*sm*/
@media (max-width: 640px) {
  .container {
    max-width: 475px;
  }
}
/*xs*/
@media (max-width: 475px) {
  .container {
    width: 100%;
  }
}
#hero {
  height: 80vh;
  display: grid;
  align-items: center;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("/media/home/001.jpg") 50% center/cover no-repeat;
  margin: 1em 1em 3em;
  border-radius: 1em;
  position: relative;
}
#hero h1 {
  text-align: center;
  color: var(--white-smoke);
  text-shadow: #000 0px 5px 10px, #000 0px 5px 10px;
  line-height: 1.2;
}
#hero h2 {
  word-spacing: 15px;
  text-align: center;
  font-family: "Arsilon";
  color: var(--white-smoke);
  text-shadow: #000 0px 5px 10px, #000 0px 5px 10px;
}

.c-t-a {
  display: flex;
  justify-content: center;
  gap: 1em;
  margin: 0.6666666667em 0 0;
}
.c-t-a a {
  background: var(--azul);
  padding: 0.6666666667em 1em;
  border-radius: calc(0.6666666667em - 4px);
  font-weight: 600;
  color: var(--white-smoke);
  box-shadow: var(--rich-black) 0px 0px 15px, var(--rich-black) 0px 0px 25px;
}

#founder {
  min-height: 40vh;
  position: relative;
  display: flex;
  align-items: center;
}
#founder .image {
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
  clip-path: polygon(45% 0%, 100% 0%, 75% 100%, 20% 100%);
}
#founder .image img {
  height: 55vh;
  width: auto;
}
#founder h4 {
  margin: 0 0 0.6666666667em;
  line-height: 1.2;
}
#founder h4 span {
  font-weight: inherit;
  position: relative;
}
#founder h4 span::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 50%;
  width: 100%;
  background: color-mix(in srgb, var(--canary), transparent 20%);
  z-index: -1;
}
#founder .verse {
  font-weight: 500;
  position: relative;
}
#founder .verse::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 50%;
  width: 100%;
  background: color-mix(in srgb, #66ff00, transparent 30%);
  z-index: -1;
}
#founder p:nth-child(4) {
  font-weight: 600;
}
#founder p:not(:nth-child(4)) {
  margin: 0 0 0.6666666667em;
}
#founder a {
  background: var(--picton-blue);
  padding: 0.6666666667em 1em;
}
#founder .text {
  max-width: 65ch;
}

#goal {
  margin-top: 6em;
  height: 100vh;
}
#goal .content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2em;
}
#goal .content > div {
  border: 2px solid var(--rich-black);
  border-radius: 1em;
  padding: 1em;
}

section {
  margin: 0 0 3em;
}